body {
  margin: 0;
  padding: 0;
}
#snake-game {
  margin: 0 auto;
}
#controls {
  position: relative;
  width: 200px;
  height: 200px;
  margin: 30px auto 0;
}
#controls > .control {
  position: absolute;
  width: 50px;
  height: 50px;
  border: solid 1px #efefef;
  border-radius: 10px;
}
#controls > .control:hover {
  cursor: pointer;
  background-color: #efefef;
}
#controls > .control:before {
  content: '';
  width: 0;
  height: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -8px;
  border: solid 8px transparent;
}
#controls > .up::before {
  margin-top: -12px;
  border-bottom-color: #999;
}
#controls > .down::before {
  margin-bottom: -12px;
  border-top-color: #999;
}
#controls > .left::before {
  margin-top: -8px;
  margin-left: -12px;
  border-right-color: #999;
}
#controls > .right::before {
  margin-top: -8px;
  margin-left: -4px;
  border-left-color: #999;
}
#controls > .up {
  top: 0;
  left: 50%;
  margin-left: -25px;
}
#controls > .down {
  bottom: 0;
  left: 50%;
  margin-left: -25px;
}
#controls > .left {
  top: 50%;
  left: 0;
  margin-top: -25px;
}
#controls > .right {
  top: 50%;
  right: 0;
  margin-top: -25px;
}



